<template>
  <a-sub-menu :key="menuInfo.path" v-bind="$props" v-on="$listeners">
    <span slot="title">
      <a-icon v-if="menuInfo.meta.icon" :type="menuInfo.meta.icon" />
      <span>{{ $t("message")[menuInfo.meta.title] }}</span>
    </span>

    <template v-for="item in menuInfo.children">
      <a-menu-item v-if="!item.children" :key="item.path" @click="changeAction(item)">
        <a-icon v-if="item.meta.icon" :type="item.meta.icon" />
        <span>{{ $t("message")[item.meta.title] }}</span>
      </a-menu-item>
      <sub-menu v-else :key="item.path" :menu-info="item" />
    </template>
  </a-sub-menu>
</template>

<script>
import { Menu } from "ant-design-vue";
export default {
  name: "SubMenu",
  isSubMenu: true,
  props: {
    ...Menu.SubMenu.props,
    menuInfo: {
      type: Object,
      default: () => ({})
    }
  },
  methods: {
    changeAction(item) {
      console.log(item);
      this.$router.push({ name: item.name, query: this.$route.query });
    }
  }
};
</script>

