<template>
  <header>
    <a-dropdown>
      <a class="ant-dropdown-link" @click="e => e.preventDefault()">
        <a-icon type="global" :style="{ fontSize: '24px' }" />
      </a>
      <a-menu slot="overlay" @click="changeAction" :selectedKeys="[locale]">
        <a-menu-item key="en_US">
          <a href="javascript:;">English</a>
        </a-menu-item>
        <a-menu-item key="zh_CN">
          <a href="javascript:;">中文</a>
        </a-menu-item>
      </a-menu>
    </a-dropdown>
  </header>
</template>

<script>
export default {
  data() {
    return {
      locale: this.$route.query.locale || "zh_CN"
    };
  },
  watch: {
    "$route.query.locale"(newVal) {
      this.locale = newVal;
    }
  },
  methods: {
    changeAction(ev) {
      if (ev.key !== this.locale) {
        this.$router.push({ query: { ...this.$route.query, locale: ev.key } });
      }
    }
  }
};
</script>

